import { Meta } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { Constants, SeoElements } from '../../core/constants';
import { UserSession } from 'core/sessions';

@Component({
  selector: 'app-termofservice',
  templateUrl: './termsofservice.component.html',
  styleUrls: ['./termsofservice.component.css']
})
export class TermsServiceComponent implements OnInit {
  public gymName = UserSession.getDynamicElements(Constants.GymName)
  public website = UserSession.getDynamicElements(Constants.website)
  public email = UserSession.getDynamicElements(Constants.email)

  constructor(private meta: Meta) {
    
    this.meta.updateTag({name : SeoElements.title, content : SeoElements.termsMetaTitle})
    this.meta.updateTag({name : SeoElements.description, content : SeoElements.termsDescription})
    this.meta.updateTag({name : SeoElements.keywords, content : SeoElements.termsKeywords})
   }

  ngOnInit() {
   
  }

}



