import { SeoElements } from './../../core/constants';
import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  constructor(private meta: Meta) {
    this.meta.updateTag({name : SeoElements.title, content : SeoElements.contactMetaTitle})
    this.meta.updateTag({name: SeoElements.description, content : SeoElements.contactDescription})
    this.meta.updateTag({name : SeoElements.keywords, content : SeoElements.contactKeywords})
   }

  ngOnInit() {
  }

}



