import { Meta } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';
import { SeoElements } from '../../core/constants';

@Component({
  selector: 'app-sendfeedback',
  templateUrl: './sendfeedback.component.html',
  styleUrls: ['./sendfeedback.component.css']
})
export class SendFeedBackComponent implements OnInit {

  constructor(private meta: Meta) {
    this.meta.updateTag({name : SeoElements.title, content : SeoElements.feedbackMetaTitle})
    this.meta.updateTag({name : SeoElements.description, content : SeoElements.feedbackDescription})
    this.meta.updateTag({name : SeoElements.keywords, content : SeoElements.feedbackKeywords})
   }

  ngOnInit() {
  }

}



