import { Constants, SeoElements } from './../../core/constants';
import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { UserSession } from 'core/sessions';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  public gymName = UserSession.getDynamicElements(Constants.GymName)

  constructor(private meta: Meta) {
    this.meta.updateTag({name : SeoElements.title, content : SeoElements.aboutusMetaTitle})
    this.meta.updateTag({name: SeoElements.description, content : SeoElements.aboutusDescription})
    this.meta.updateTag({name : SeoElements.keywords, content : SeoElements.aboutusKeywords})
   }

  ngOnInit() {
  }

}



