import { Constants, SeoElements } from './../../core/constants';
import { Component, OnInit } from '@angular/core';
import { Meta } from '@angular/platform-browser';
import { UserSession } from 'core/sessions';

@Component({
  selector: 'app-privacypolicy',
  templateUrl: './privacypolicy.component.html',
  styleUrls: ['./privacypolicy.component.css']
})
export class PrivacyPolicyComponent implements OnInit {
  public gymName = UserSession.getDynamicElements(Constants.GymName)
  public website = UserSession.getDynamicElements(Constants.website)
  public email = UserSession.getDynamicElements(Constants.email)

  constructor(private meta: Meta) {
    this.meta.updateTag({name: SeoElements.title, content : SeoElements.privacyMetaTitle})
    this.meta.updateTag({name: SeoElements.description, content: SeoElements.privacyDescription})
    this.meta.updateTag({name: SeoElements.keywords, content: SeoElements.privacyKeywords})
   }

  ngOnInit() {
  }

}



